.products {
  font-family: $font-Roboto;
  color: #ffffff;
}

.products__list {
  @include list-reset;
  margin: 0 auto;
  max-width: 1440px;
  padding: 0 135px;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  box-sizing: border-box;
}

.products__title {
  margin: 0 auto;
  max-width: 1440px;
  margin-top: 93px;
  margin-bottom: 47px;
  padding: 0 135px;

  font-weight: normal;
  font-size: 36px;
  line-height: 51px;

  box-sizing: border-box;
}

.products__link {
  color: #ffffff;
  text-decoration: none;
  font-size: 20px;
  line-height: 30px;

  padding: 53px 0;
  padding-right: 34%;
  padding-left: 44px;
  display: block;
  width: 100%;
  height: 100%;
  box-sizing: border-box;

  position: relative;
  z-index: 1;
}

.products__item {
  width: 32%;
  min-height: 620px;

  position: relative;
  box-sizing: border-box;

  display: flex;
  flex-wrap: wrap;

  cursor: pointer;

  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.products__item--production {
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 620px;
    background-image: url("../img/products-image1.png");
    background-repeat: no-repeat;
    background-size: 370px 620px;
    background-position: 0 50%;
    left: 0;
    top: 0;
  }
}

.products__item--installation {
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 620px;
    background-image: url("../img/products-image2.png");
    background-repeat: no-repeat;
    background-size: 370px 620px;
    background-position: 0 50%;
    left: 0;
    top: 0;
  }
}

.products__item--accessories {
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 620px;
    background-image: url("../img/products-image3.png");
    background-repeat: no-repeat;
    background-size: 370px 620px;
    background-position: 0 50%;
    left: 0;
    top: 0;
  }
}

.products__button {
  display: block;
  width: 63px;
  height: 63px;
  background-color: $blue;
  margin-top: auto;
  margin-left: auto;

  position: absolute;
  bottom: 0;
  right: 0;

  &::before {
    content: "";
    position: absolute;

    width: 8px;
    height: 8px;
    left: 27px;
    top: 27px;

    border-right: 1px solid #ffffff;
    border-bottom: 1px solid #ffffff;
    transform: rotate(-45deg);
  }
}

@media (max-width: $desktop-width) {
  .products__list {
    @include padding;

    max-width: 1023px;
  }

  .products__title {
    @include padding;
    padding-right: 135px;

    max-width: 1023px;

    margin-top: 53px;
    margin-bottom: 0;

    font-size: 32px;
    line-height: 51px;
  }

  .products__item {
    margin-top: 46px;
    width: 100%;
    min-height: 256px;
  }

  .products__item--production {
    &::before {
      height: 256px;
      background-image: url("../img/products-tablet-image1.png");
      background-repeat: repeat-x;
      background-size: 770px 301px;
      background-position: 0 50%;
    }
  }

  .products__item--installation {
    &::before {
      height: 256px;
      background-image: url("../img/products-tablet-image2.png");
      background-repeat: repeat-x;
      background-size: 770px 280px;
      background-position: 50% 0;
    }
  }

  .products__item--accessories {
    &::before {
      height: 256px;
      background-image: url("../img/products-tablet-image3.png");
      background-repeat: repeat-x;
      background-size: 770px 256px;
      background-position: 0 50%;
    }
  }

  .products__link {
    padding-right: 64%;
  }
}

@media (max-width: $tablet-width) {
  .products__list {
    max-width: 767px;
  }

  .products__title {
    padding-left: 25px;
    padding-right: 0;

    max-width: 767px;

    margin-bottom: 7px;

    font-size: 28px;
    line-height: 141%;
  }

  .products__item {
    margin-top: 30px;
  }

  .products__link {
    padding: 23px;
    padding-right: 46%;
    padding-left: 35px;
  }
}
